import angular from 'angular';

import angularDecorator, {APP_NAME} from '../../.storybook/angular-decorator';

import Selection from '@jetbrains/ring-ui/components/data-list/selection';
import data from '@jetbrains/ring-ui/components/data-list/data-list.mock';

import RingDataList from '@jetbrains/ring-ui/components/data-list-ng/data-list-ng';

export default {
  title: 'Legacy Angular/DataList Ng',
  decorators: [angularDecorator()],

  parameters: {"storySource":{"source":"import angular from 'angular';\n\nimport angularDecorator, {APP_NAME} from '../../.storybook/angular-decorator';\n\nimport Selection from '@jetbrains/ring-ui/components/data-list/selection';\nimport data from '@jetbrains/ring-ui/components/data-list/data-list.mock';\n\nimport RingDataList from '@jetbrains/ring-ui/components/data-list-ng/data-list-ng';\n\nexport default {\n  title: 'Legacy Angular/DataList Ng',\n  decorators: [angularDecorator()],\n\n  parameters: {\n    notes: 'Provides an Angular wrapper for DataList.'\n  }\n};\n\nexport const basic = () => {\n  angular.module(APP_NAME, [RingDataList]).controller('testCtrl', function controller() {\n    function itemFormatter(item) {\n      return {\n        ...item,\n        collapsible: false\n      };\n    }\n\n    const selection = new Selection({\n      data,\n      isItemSelectable: item => item.selectable,\n      getChildren: item => item.items || []\n    });\n\n    this.data = data;\n    this.itemFormatter = itemFormatter;\n    this.selection = selection;\n    this.onSelect = newSelection => {\n      this.selection = newSelection;\n    };\n  });\n\n  return `\n      <div ng-controller=\"testCtrl as ctrl\">\n        <rg-data-list\n          data=\"ctrl.data\"\n          selection=\"ctrl.selection\"\n          on-select=\"ctrl.onSelect\"\n          item-formatter=\"ctrl.itemFormatter\"\n        ></rg-data-list>\n      </div>\n    `;\n};\n\nbasic.storyName = 'DataList Ng';\n","locationsMap":{"basic":{"startLoc":{"col":21,"line":19},"endLoc":{"col":1,"line":52},"startBody":{"col":21,"line":19},"endBody":{"col":1,"line":52}}}},
    notes: 'Provides an Angular wrapper for DataList.'
  },};

export const basic = () => {
  angular.module(APP_NAME, [RingDataList]).controller('testCtrl', function controller() {
    function itemFormatter(item) {
      return {
        ...item,
        collapsible: false
      };
    }

    const selection = new Selection({
      data,
      isItemSelectable: item => item.selectable,
      getChildren: item => item.items || []
    });

    this.data = data;
    this.itemFormatter = itemFormatter;
    this.selection = selection;
    this.onSelect = newSelection => {
      this.selection = newSelection;
    };
  });

  return `
      <div ng-controller="testCtrl as ctrl">
        <rg-data-list
          data="ctrl.data"
          selection="ctrl.selection"
          on-select="ctrl.onSelect"
          item-formatter="ctrl.itemFormatter"
        ></rg-data-list>
      </div>
    `;
};

basic.storyName = 'DataList Ng';
