import angular from 'angular';

import angularDecorator, {APP_NAME} from '../../.storybook/angular-decorator';

import CompilerNG from '@jetbrains/ring-ui/components/compiler-ng/compiler-ng';

export default {
  title: 'Legacy Angular/Compiler Ng',
  decorators: [angularDecorator()],

  parameters: {"storySource":{"source":"import angular from 'angular';\n\nimport angularDecorator, {APP_NAME} from '../../.storybook/angular-decorator';\n\nimport CompilerNG from '@jetbrains/ring-ui/components/compiler-ng/compiler-ng';\n\nexport default {\n  title: 'Legacy Angular/Compiler Ng',\n  decorators: [angularDecorator()],\n\n  parameters: {\n    hermione: {skip: true}\n  }\n};\n\nexport const basic = () => {\n  angular.module(APP_NAME, [CompilerNG]).run(function controller($rootScope, rgCompiler) {\n    const $scope = $rootScope.$new();\n    $scope.testValue = 'Hello from compiled node';\n\n    rgCompiler({template: '<div>{{testValue}}</div>'}).then(data => {\n      data.link($scope);\n      setTimeout(() => {\n        document.getElementById('for-compiled').appendChild(data.element[0]);\n      });\n    });\n  });\n\n  return '<div id=\"for-compiled\"></div>';\n};\n\nbasic.storyName = 'Compiler Ng';\n","locationsMap":{"basic":{"startLoc":{"col":21,"line":16},"endLoc":{"col":1,"line":30},"startBody":{"col":21,"line":16},"endBody":{"col":1,"line":30}}}},
    hermione: {skip: true}
  },};

export const basic = () => {
  angular.module(APP_NAME, [CompilerNG]).run(function controller($rootScope, rgCompiler) {
    const $scope = $rootScope.$new();
    $scope.testValue = 'Hello from compiled node';

    rgCompiler({template: '<div>{{testValue}}</div>'}).then(data => {
      data.link($scope);
      setTimeout(() => {
        document.getElementById('for-compiled').appendChild(data.element[0]);
      });
    });
  });

  return '<div id="for-compiled"></div>';
};

basic.storyName = 'Compiler Ng';
