import React, {Component} from 'react';

import reactDecorator from '../../.storybook/react-decorator';

import Theme from '@jetbrains/ring-ui/components/global/theme';

import ProgressBar from '@jetbrains/ring-ui/components/progress-bar/progress-bar';

const disableAnimations = window.location.search.includes('block-animations');

export default {parameters: {"storySource":{"source":"import React, {Component} from 'react';\n\nimport reactDecorator from '../../.storybook/react-decorator';\n\nimport Theme from '@jetbrains/ring-ui/components/global/theme';\n\nimport ProgressBar from '@jetbrains/ring-ui/components/progress-bar/progress-bar';\n\nconst disableAnimations = window.location.search.includes('block-animations');\n\nexport default {\n  title: 'Components/Progress Bar',\n  decorators: [reactDecorator()]\n};\n\nexport const basic = () => {\n  class ProgressBarDemo extends Component {\n    state = {\n      value: disableAnimations ? 0.5 : 0\n    };\n\n    componentDidMount() {\n      if (disableAnimations) {\n        return;\n      }\n      setInterval(\n        () => this.setState(({value}) => ({value: value >= 1 ? 0 : value + 0.1})),\n        500\n      );\n    }\n\n    render() {\n      const {value} = this.state;\n\n      return (\n        <div>\n          <div style={{height: '25px', paddingTop: '25px'}}>\n            <ProgressBar label=\"Progress\" value={value} style={{width: 288}}/>\n          </div>\n\n          <div style={{height: '25px', paddingTop: '25px', background: '#000'}}>\n            <ProgressBar label=\"Progress\" value={value} theme={Theme.DARK} style={{width: 288}}/>\n          </div>\n\n          <div style={{height: '25px', paddingTop: '25px', background: '#F0F0F0'}}>\n            <ProgressBar label=\"Progress\" value={value} style={{width: 288}}/>\n          </div>\n        </div>\n      );\n    }\n  }\n\n  return <ProgressBarDemo/>;\n};\n\nbasic.storyName = 'Progress Bar';\n","locationsMap":{"basic":{"startLoc":{"col":21,"line":16},"endLoc":{"col":1,"line":54},"startBody":{"col":21,"line":16},"endBody":{"col":1,"line":54}}}},},
  title: 'Components/Progress Bar',
  decorators: [reactDecorator()]
};

export const basic = () => {
  class ProgressBarDemo extends Component {
    state = {
      value: disableAnimations ? 0.5 : 0
    };

    componentDidMount() {
      if (disableAnimations) {
        return;
      }
      setInterval(
        () => this.setState(({value}) => ({value: value >= 1 ? 0 : value + 0.1})),
        500
      );
    }

    render() {
      const {value} = this.state;

      return (
        <div>
          <div style={{height: '25px', paddingTop: '25px'}}>
            <ProgressBar label="Progress" value={value} style={{width: 288}}/>
          </div>

          <div style={{height: '25px', paddingTop: '25px', background: '#000'}}>
            <ProgressBar label="Progress" value={value} theme={Theme.DARK} style={{width: 288}}/>
          </div>

          <div style={{height: '25px', paddingTop: '25px', background: '#F0F0F0'}}>
            <ProgressBar label="Progress" value={value} style={{width: 288}}/>
          </div>
        </div>
      );
    }
  }

  return <ProgressBarDemo/>;
};

basic.storyName = 'Progress Bar';
