import angular from 'angular';

import angularDecorator, {APP_NAME} from '../../.storybook/angular-decorator';

import RingPager from '@jetbrains/ring-ui/components/pager-ng/pager-ng';

export default {
  title: 'Legacy Angular/Pager Ng',
  decorators: [angularDecorator()],

  parameters: {"storySource":{"source":"import angular from 'angular';\n\nimport angularDecorator, {APP_NAME} from '../../.storybook/angular-decorator';\n\nimport RingPager from '@jetbrains/ring-ui/components/pager-ng/pager-ng';\n\nexport default {\n  title: 'Legacy Angular/Pager Ng',\n  decorators: [angularDecorator()],\n\n  parameters: {\n    notes: 'Provides an Angular wrapper for Pager.'\n  }\n};\n\nexport const basic = () => {\n  angular.module(APP_NAME, [RingPager]).controller('testCtrl', function controller() {\n    this.total = 750;\n    this.currentPage = 1;\n\n    this.onPageChange = page => {\n      this.currentPage = page;\n    };\n  });\n\n  return `\n      <div ng-controller=\"testCtrl as ctrl\">\n        <rg-pager\n          total=\"ctrl.total\"\n          current-page=\"ctrl.currentPage\"\n          on-page-change=\"ctrl.onPageChange\"\n        ></rg-pager>\n      </div>\n    `;\n};\n\nbasic.storyName = 'Pager Ng';\n","locationsMap":{"basic":{"startLoc":{"col":21,"line":16},"endLoc":{"col":1,"line":35},"startBody":{"col":21,"line":16},"endBody":{"col":1,"line":35}}}},
    notes: 'Provides an Angular wrapper for Pager.'
  },};

export const basic = () => {
  angular.module(APP_NAME, [RingPager]).controller('testCtrl', function controller() {
    this.total = 750;
    this.currentPage = 1;

    this.onPageChange = page => {
      this.currentPage = page;
    };
  });

  return `
      <div ng-controller="testCtrl as ctrl">
        <rg-pager
          total="ctrl.total"
          current-page="ctrl.currentPage"
          on-page-change="ctrl.onPageChange"
        ></rg-pager>
      </div>
    `;
};

basic.storyName = 'Pager Ng';
