export const basic = () => {
  class PagerDemo extends Component {
    state = {
      total: 750,
      currentPage: 1
    };

    render() {
      const {total, currentPage} = this.state;
      return (
        <Pager
          total={total}
          currentPage={currentPage}
          disablePageSizeSelector
          onPageChange={cp => this.setState({currentPage: cp})}
        />
      );
    }
  }

  return <PagerDemo/>;
};;

