import angular from 'angular';

import angularDecorator, {APP_NAME} from '../../.storybook/angular-decorator';

import RingProgressBar from '@jetbrains/ring-ui/components/progress-bar-ng/progress-bar-ng';

const disableAnimations = window.location.search.includes('block-animations');

export default {
  title: 'Legacy Angular/Progress Bar Ng',
  decorators: [angularDecorator()],

  parameters: {"storySource":{"source":"import angular from 'angular';\n\nimport angularDecorator, {APP_NAME} from '../../.storybook/angular-decorator';\n\nimport RingProgressBar from '@jetbrains/ring-ui/components/progress-bar-ng/progress-bar-ng';\n\nconst disableAnimations = window.location.search.includes('block-animations');\n\nexport default {\n  title: 'Legacy Angular/Progress Bar Ng',\n  decorators: [angularDecorator()],\n\n  parameters: {\n    notes: 'Provides an Angular wrapper for Progress Bar.'\n  }\n};\n\nexport const basic = () => {\n  angular.\n    module(APP_NAME, [RingProgressBar]).\n    controller('ExampleCtrl', function controller($interval) {\n      this.value = disableAnimations ? 0.5 : 0;\n\n      if (!disableAnimations) {\n        $interval(() => {\n          const currentValue = this.value;\n          this.value = currentValue >= 1 ? 0 : currentValue + 0.1;\n        }, 500);\n      }\n    });\n  return `\n      <div ng-controller=\"ExampleCtrl as ctrl\">\n        <div style=\"height: 25px; padding-top: 25px;\">\n          <rg-progress-bar label=\"'Progress'\" value=\"ctrl.value\" class=\"example-progress\"></rg-progress-bar>\n        </div>\n        <div style=\"height: 25px; background: #000; padding-top: 25px;\">\n          <rg-progress-bar label=\"'Progress'\" value=\"ctrl.value\" theme=\"'dark'\" class=\"example-progress\"></rg-progress-bar>\n        </div>\n        <div style=\"height: 25px; background: #F0F0F0; padding-top: 25px;\">\n          <rg-progress-bar label=\"'Progress'\" value=\"ctrl.value\" class=\"example-progress\"></rg-progress-bar>\n        </div>\n      </div>\n    `;\n};\n\nbasic.storyName = 'Progress Bar Ng';\n\nbasic.parameters = {\n  storyStyles: `\n<style>\n  .example-progress > * {\n    width: 288px;\n  }\n</style>`\n};\n","locationsMap":{"basic":{"startLoc":{"col":21,"line":18},"endLoc":{"col":1,"line":44},"startBody":{"col":21,"line":18},"endBody":{"col":1,"line":44}}}},
    notes: 'Provides an Angular wrapper for Progress Bar.'
  },};

export const basic = () => {
  angular.
    module(APP_NAME, [RingProgressBar]).
    controller('ExampleCtrl', function controller($interval) {
      this.value = disableAnimations ? 0.5 : 0;

      if (!disableAnimations) {
        $interval(() => {
          const currentValue = this.value;
          this.value = currentValue >= 1 ? 0 : currentValue + 0.1;
        }, 500);
      }
    });
  return `
      <div ng-controller="ExampleCtrl as ctrl">
        <div style="height: 25px; padding-top: 25px;">
          <rg-progress-bar label="'Progress'" value="ctrl.value" class="example-progress"></rg-progress-bar>
        </div>
        <div style="height: 25px; background: #000; padding-top: 25px;">
          <rg-progress-bar label="'Progress'" value="ctrl.value" theme="'dark'" class="example-progress"></rg-progress-bar>
        </div>
        <div style="height: 25px; background: #F0F0F0; padding-top: 25px;">
          <rg-progress-bar label="'Progress'" value="ctrl.value" class="example-progress"></rg-progress-bar>
        </div>
      </div>
    `;
};

basic.storyName = 'Progress Bar Ng';

basic.parameters = {
  storyStyles: `
<style>
  .example-progress > * {
    width: 288px;
  }
</style>`
};
