import angular from 'angular';

import angularDecorator, {APP_NAME} from '../../.storybook/angular-decorator';

import TableLegacyToolbar from '@jetbrains/ring-ui/components/table-legacy-ng/table-legacy-ng__toolbar';
import Selection from '@jetbrains/ring-ui/components/table/selection';
import data from '@jetbrains/ring-ui/components/table/table.examples.json';

import TableNG from '@jetbrains/ring-ui/components/table-ng/table-ng';

export default {
  title: 'Legacy Angular/Table Ng',
  decorators: [angularDecorator()],

  parameters: {"storySource":{"source":"import angular from 'angular';\n\nimport angularDecorator, {APP_NAME} from '../../.storybook/angular-decorator';\n\nimport TableLegacyToolbar from '@jetbrains/ring-ui/components/table-legacy-ng/table-legacy-ng__toolbar';\nimport Selection from '@jetbrains/ring-ui/components/table/selection';\nimport data from '@jetbrains/ring-ui/components/table/table.examples.json';\n\nimport TableNG from '@jetbrains/ring-ui/components/table-ng/table-ng';\n\nexport default {\n  title: 'Legacy Angular/Table Ng',\n  decorators: [angularDecorator()],\n\n  parameters: {\n    notes: 'Provides an Angular wrapper for Table.'\n  }\n};\n\nexport const basic = () => {\n  angular.\n    module(APP_NAME, [TableNG, TableLegacyToolbar]).\n    controller('testCtrl', function controller() {\n      const columns = [\n        {\n          id: 'country',\n          title: 'Country'\n        },\n        {\n          id: 'city',\n          title: 'City',\n          sortable: true\n        },\n        {\n          id: 'url',\n          title: 'URL'\n        }\n      ];\n\n      this.data = data;\n      this.columns = columns;\n      this.selection = new Selection({data});\n      this.sortKey = 'city';\n      this.sortOrder = false;\n\n      this.onSelect = selection => {\n        this.selection = selection;\n      };\n\n      this.onSort = ({column: {id: key}, order}) => {\n        this.sortOrder = order;\n        this.data = this.data.\n          slice().\n          sort((itemA, itemB) =>\n            (order ? itemA[key].localeCompare(itemB[key]) : itemB[key].localeCompare(itemA[key]))\n          );\n      };\n    });\n\n  return `\n      <div ng-controller=\"testCtrl as ctrl\">\n        <h3>Title of the page</h3>\n\n        <rg-legacy-table-toolbar stick>\n          <div>Some toolbar content. Focused: {{ctrl.selection.getFocused().country}}</div>\n        </rg-legacy-table-toolbar>\n\n        <rg-table\n          data=\"ctrl.data\"\n          columns=\"ctrl.columns\"\n          selection=\"ctrl.selection\"\n          on-select=\"ctrl.onSelect\"\n          autofocus=\"true\"\n          sticky-header-offset=\"'56px'\"\n\n          sort-key=\"ctrl.sortKey\"\n          sort-order=\"ctrl.sortOrder\"\n          on-sort=\"ctrl.onSort\"\n        ></rg-table>\n      </div>\n    `;\n};\n\nbasic.storyName = 'Table Ng';\n","locationsMap":{"basic":{"startLoc":{"col":21,"line":20},"endLoc":{"col":1,"line":82},"startBody":{"col":21,"line":20},"endBody":{"col":1,"line":82}}}},
    notes: 'Provides an Angular wrapper for Table.'
  },};

export const basic = () => {
  angular.
    module(APP_NAME, [TableNG, TableLegacyToolbar]).
    controller('testCtrl', function controller() {
      const columns = [
        {
          id: 'country',
          title: 'Country'
        },
        {
          id: 'city',
          title: 'City',
          sortable: true
        },
        {
          id: 'url',
          title: 'URL'
        }
      ];

      this.data = data;
      this.columns = columns;
      this.selection = new Selection({data});
      this.sortKey = 'city';
      this.sortOrder = false;

      this.onSelect = selection => {
        this.selection = selection;
      };

      this.onSort = ({column: {id: key}, order}) => {
        this.sortOrder = order;
        this.data = this.data.
          slice().
          sort((itemA, itemB) =>
            (order ? itemA[key].localeCompare(itemB[key]) : itemB[key].localeCompare(itemA[key]))
          );
      };
    });

  return `
      <div ng-controller="testCtrl as ctrl">
        <h3>Title of the page</h3>

        <rg-legacy-table-toolbar stick>
          <div>Some toolbar content. Focused: {{ctrl.selection.getFocused().country}}</div>
        </rg-legacy-table-toolbar>

        <rg-table
          data="ctrl.data"
          columns="ctrl.columns"
          selection="ctrl.selection"
          on-select="ctrl.onSelect"
          autofocus="true"
          sticky-header-offset="'56px'"

          sort-key="ctrl.sortKey"
          sort-order="ctrl.sortOrder"
          on-sort="ctrl.onSort"
        ></rg-table>
      </div>
    `;
};

basic.storyName = 'Table Ng';
