import angular from 'angular';

import angularDecorator, {APP_NAME} from '../../.storybook/angular-decorator';

import hubConfig from '../../.storybook/hub-config';

import AuthNG from '@jetbrains/ring-ui/components/auth-ng/auth-ng';

import QueryAssistNG from '@jetbrains/ring-ui/components/query-assist-ng/query-assist-ng';

export default {
  title: 'Legacy Angular/Query Assist Ng',
  decorators: [angularDecorator()],

  parameters: {"storySource":{"source":"import angular from 'angular';\n\nimport angularDecorator, {APP_NAME} from '../../.storybook/angular-decorator';\n\nimport hubConfig from '../../.storybook/hub-config';\n\nimport AuthNG from '@jetbrains/ring-ui/components/auth-ng/auth-ng';\n\nimport QueryAssistNG from '@jetbrains/ring-ui/components/query-assist-ng/query-assist-ng';\n\nexport default {\n  title: 'Legacy Angular/Query Assist Ng',\n  decorators: [angularDecorator()],\n\n  parameters: {\n    notes: 'Provides an Angular wrapper for Query Assist.',\n    hermione: {skip: true}\n  }\n};\n\nexport const basic = ({onChange}) => {\n  angular.\n    module(APP_NAME, [QueryAssistNG, AuthNG]).\n    config(authProvider => {\n      authProvider.config(hubConfig);\n    }).\n    controller('testCtrl', function ctrl($http) {\n      this.queries = [];\n      this.query = 'query';\n      this.focus = true;\n      this.disabled = true;\n\n      this.save = ({query}) => {\n        this.queries.unshift(query);\n      };\n\n      this.change = ({query}) => {\n        this.query = query;\n        onChange('Query = ', query);\n      };\n\n      this.focusChange = ({focus}) => {\n        this.focus = focus;\n      };\n\n      this.source = ({query, caret, omitSuggestions}) => {\n        const config = {\n          params: {\n            fields: `query,caret,styleRanges${omitSuggestions ? '' : ',suggestions'}`,\n            query,\n            caret\n          }\n        };\n\n        return $http.\n          get(`${hubConfig.serverUri}/api/rest/users/queryAssist`, config).\n          then(data => data.data);\n      };\n    });\n\n  return `\n      <div ng-controller=\"testCtrl as ctrl\">\n        <button ng-click=\"ctrl.disabled = !ctrl.disabled\">Disable/Enable</button>\n\n        <div>\n          <p>{{ ctrl.query || 'no value' }}</p>\n\n          <rg-query-assist\n            x-clear=\"true\"\n            x-data-source=\"ctrl.source\"\n            x-disabled=\"ctrl.disabled\"\n            glass=\"true\"\n            focus=\"ctrl.focus\"\n            query=\"ctrl.query\"\n            on-apply=\"ctrl.save\"\n            on-change=\"ctrl.change\"\n            on-focus-change=\"ctrl.focusChange\"\n            placeholder=\"'placeholder'\"\n            hint=\"'Press ⇥ to complete first item'\"\n            hint-on-selection=\"'Press ↩ to complete selected item'\"></rg-query-assist>\n\n          <p ng-repeat=\"query in ctrl.queries track by $index\">{{ query }}</p>\n        </div>\n      </div>\n    `;\n};\n\nbasic.storyName = 'Query Assist Ng';\nbasic.argTypes = {onChange: {}};\n","locationsMap":{"basic":{"startLoc":{"col":21,"line":21},"endLoc":{"col":1,"line":86},"startBody":{"col":21,"line":21},"endBody":{"col":1,"line":86}}}},
    notes: 'Provides an Angular wrapper for Query Assist.',
    hermione: {skip: true}
  },};

export const basic = ({onChange}) => {
  angular.
    module(APP_NAME, [QueryAssistNG, AuthNG]).
    config(authProvider => {
      authProvider.config(hubConfig);
    }).
    controller('testCtrl', function ctrl($http) {
      this.queries = [];
      this.query = 'query';
      this.focus = true;
      this.disabled = true;

      this.save = ({query}) => {
        this.queries.unshift(query);
      };

      this.change = ({query}) => {
        this.query = query;
        onChange('Query = ', query);
      };

      this.focusChange = ({focus}) => {
        this.focus = focus;
      };

      this.source = ({query, caret, omitSuggestions}) => {
        const config = {
          params: {
            fields: `query,caret,styleRanges${omitSuggestions ? '' : ',suggestions'}`,
            query,
            caret
          }
        };

        return $http.
          get(`${hubConfig.serverUri}/api/rest/users/queryAssist`, config).
          then(data => data.data);
      };
    });

  return `
      <div ng-controller="testCtrl as ctrl">
        <button ng-click="ctrl.disabled = !ctrl.disabled">Disable/Enable</button>

        <div>
          <p>{{ ctrl.query || 'no value' }}</p>

          <rg-query-assist
            x-clear="true"
            x-data-source="ctrl.source"
            x-disabled="ctrl.disabled"
            glass="true"
            focus="ctrl.focus"
            query="ctrl.query"
            on-apply="ctrl.save"
            on-change="ctrl.change"
            on-focus-change="ctrl.focusChange"
            placeholder="'placeholder'"
            hint="'Press ⇥ to complete first item'"
            hint-on-selection="'Press ↩ to complete selected item'"></rg-query-assist>

          <p ng-repeat="query in ctrl.queries track by $index">{{ query }}</p>
        </div>
      </div>
    `;
};

basic.storyName = 'Query Assist Ng';
basic.argTypes = {onChange: {}};
